@import "../../../scss/index.scss";

.ui.modal.basic-modal {
    background-color: $background-grey-dark;
    border-radius: 8px;

    .header{
        text-align: center;
        color: $font-light;
        background-color: transparent;
        h3{
            margin: 0;
        }
        i{
            position: absolute;
            right: 10px;
            top: 16px;

            &:hover{
                cursor: pointer;
            }
        }
    }

    .content{
        color: $font-light;
        background-color: transparent;
    }
}